body,html {
  padding: 0;
  margin: 0;
}

/* a-scene {
  height: 100vh;
  width: 100vw;
} */
.App {
  position: relative;
  display: inline-flex;
  /* height: 100vh;
  width: 100vw; */
  margin: 0;
  padding: 0;
  text-align: center;
}

.text-info {
  font-size: 30px
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
